@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
html {
  font-size: 10px;
}

body {
  margin: 0;
  font-family: "Roboto Light",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(
          linear,
          left top, left bottom,
          from(rgba(0, 0, 0, 0.5)),
          to(rgba(0, 0, 0, 0.5))
  ),
  url(/sberform/static/media/purple_sunset-wallpaper-2880x1800.a3f99fa9.png) no-repeat center center fixed;
  background: linear-gradient(
          rgba(0, 0, 0, 0.5),
          rgba(0, 0, 0, 0.5)
  ),
  url(/sberform/static/media/purple_sunset-wallpaper-2880x1800.a3f99fa9.png) no-repeat center center fixed;
  background-size: cover;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

svg {
  fill: black;
}
.login-form {
  display: flex;
  position: relative;
  flex-flow: column;
  justify-content: space-around;
  margin-top: 1.5rem;
  height: 265px;
  width: 370px;
}

.login-form__input {
  width: 367px;
  height: 46px;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.35);
  text-align: center;
  font-size: 1.8rem;
}

.login-form__input-valid {
  background: none;
  color: rgba(255, 255, 255, 0.8);
}

.login-form__input-error {
  position: absolute;
  top: 21px;
  right: 10px;
}

.login-form__input-check {
  position: absolute;
  top: 28px;
  right: 17px;
}

.login-form__input-password:not(:focus) {
  background: none;
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.8);
}

.login-form__input-password:placeholder-shown {
  font-size: 1.8rem;
}

.login-form__input-password:focus::-webkit-input-placeholder {
  color:transparent;
}

.login-form__input-password:focus::-moz-placeholder {
  color:transparent;
}

.login-form__input-password:focus:-ms-input-placeholder {
  color:transparent;
}

.login-form__input-password:focus::-ms-input-placeholder {
  color:transparent;
}

.login-form__input-password:focus::placeholder {
  color:transparent;
}

.login-form__input-password:focus {
  font-size: 3rem;
  background: white;
}

.login-form__remember {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2.5rem;
  color: rgba(255, 255, 255, 0.8);
}

.login-form__checkbox {
  visibility: hidden;
  height: 0;
  width: 0;
}

.login-form__checkbox-container {
  display: flex;
  align-items: center;
  font-size: 1.6rem;
}

.login-form__checkbox-label {
  display: block;
  position: relative;
  width: 42px;
  height: 22px;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  cursor: pointer;
}

.login-form__checkbox-label:after {
  content: '';
  display: block;
  position: absolute;
  top: 3px;
  left: 4px;
  width: 22px;
  height: 16px;
  border: none;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 25px;
  -webkit-transition: .2s;
  transition: .2s;
}

.login-form__checkbox:checked + label:after {
  left: 16px;
  background-color: rgba(255, 255, 255, 1);
}

.login-form__checkbox-label_text {
  cursor: pointer;
  padding-left: 1rem;
}

.login-form__forgot-password {
  display: inline-block;
  float: right;
  text-decoration: none;
  font-size: 1.6rem;
  color: rgba(255, 255, 255, 0.8);
  border-bottom: 1px solid rgba(255, 255, 255, 0.8);
}

.login-form__submit {
  color: white;
  align-self: center;
  background: none;
  width: 151px;
  height: 48px;
  border-radius: 25px;
  border: 1px solid white;
  font-size: 1.2rem;
  letter-spacing: 0.3rem;
}

.login-form__submit:disabled {
  color: rgba(255, 255, 255, 0.35);
  border: 1px solid rgba(255, 255, 255, 0.35);
  background: none;
}

.login-form__submit:active,
.login-form__submit:focus {
  color: black;
  background-color: white;
}

.wrapper {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  display: flex;
  position: relative;
  flex-flow: column;
  align-items: center;
}

.padding {
  padding: 3rem;
}

.App {
  width: 626px;
  height: 434px;
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border-radius: 3px;
  color: #999999;
  z-index: 99;
}

.blur {
  position: absolute;
  background: url(/sberform/static/media/purple_sunset-wallpaper-2880x1800.a3f99fa9.png) no-repeat center center fixed;
  background-size: cover;
  overflow: hidden;
  width: 626px;
  height: 434px;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

.App-logo {
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.return {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 0.1rem;
}

.arrow {
  padding-right: .6rem;
}

.clip-circle {
  position: absolute;
  top: -50px;
  -webkit-clip-path: circle(60px at center);
          clip-path: circle(60px at center);
}
